.base-stats-controls {
  margin: 15px;
}

.base-stats-controls button {
  margin: 15px;
}

.base-stats-fields {
  display: grid;
  grid-template-columns: auto auto auto;
}

.base-stats-fields div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.base-stats-fields div * {
  margin: 5px 15px;
  width: 30px;
}

.base-stats-fields div button {
  width: auto;
}

@media (min-width: 1280px) {
  .base-stats-fields {
    display: grid;
    grid-template-columns: auto auto;
  }

  .base-stats-fields div * {
    margin: 5px 15px;
    width: 30px;
  }
}
